import React from "react";
import PropTypes from "prop-types";
import galleryData from "./GalleryData";
import popupData from "./PopupData";
const Grid = ({ size, openPopup }) => (
  <div
    className={
      "grid " + (size == "mobile" ? "" : size == "tablet" ? "t-grid" : "")
    }
  >
    {galleryData.map((data, index) => (
      <div key={index} className={`${data.type} box`}>
        <div className="top-card card-detail">
          {data.project}
          <span>{data.type}</span>
        </div>
        <div
          onClick={() => openPopup(popupData(index, data))}
          className="bottom-card card-detail"
        >
          LEARN MORE
        </div>
        <div className="img" />
      </div>
    ))}
  </div>
);
Grid.propTypes = {
  size: PropTypes.string.isRequired,
  openPopup: PropTypes.func.isRequired
};
export default Grid;



