import React from "react";
import PropTypes from "prop-types";
import logo from "../assets/colored.svg";
const Header = ({
  // wheeling,
  current,
  mouse,
  ArrowUp,
  ArrowDown,
  ArrowLeft,
  ArrowRight,
  size,
  authenticated,
  mobileNav,
  popupOpen,
  navOpen,
  scrollTo
}) => {
  if (size !== "mobile") {
    return (
      <header
        className="header"
        style={{ height: current > 0 || size == "tablet" ? 55 : 100 }}
      >
        <div className="item logo1">
          <a onClick={() => scrollTo(0)}>
            <img
              style={{
                height: current > 0 || size == "tablet" ? "" : 50
              }}
              src={logo}
            />
          </a>
        </div>
        <div className="item links">
          <ul>
            <a
              onClick={() => scrollTo(1)}
              style={{
                color: current == 1 && "#3bad8a"
              }}
            >
              Portfolio
            </a>
            <a
              onClick={() => scrollTo(2)}
              style={{
                color: current == 2 && "#3bad8a"
              }}
            >
              About
            </a>
            <a
              onClick={() => scrollTo(3)}
              style={{
                color: current == 3 && "#3bad8a"
              }}
            >
              Contact
            </a>
          </ul>
        </div>
        <div className="item navInfo">
          <span className={"mice " + (mouse ? "active" : "")} />
          <div className="arrows">
            <span className={"arrow a_up " + (ArrowUp && "active")} />
            <span className={"arrow a_left " + (ArrowLeft && "active")} />
            <span className={"arrow a_down " + (ArrowDown && "active")} />
            <span className={"arrow a_right " + (ArrowRight && "active")} />
          </div>
        </div>
      </header>
    );
  }
  return (
    <nav>
      <div
        className="n-overlay"
        onClick={navOpen}
        style={{
          opacity: mobileNav && 1,
          transform: mobileNav || "translate3d(-100%, 0px, 0px)",
          transition:
            mobileNav || "opacity 0.5s ease 0s, transform 0s ease 0.5s"
        }}
      />
      <div className="bm-menu wrap" style={{ transform: mobileNav && "none" }}>
        <i className="fas fa-times exit-menu" onClick={navOpen} />
      </div>
      <div
        onClick={navOpen}
        className="bm-burger-button"
        style={{
          transform: authenticated && "none",
          opacity: popupOpen && 0
        }}
      >
        <span className="bm-burger-bars bar1" />
        <span className="bm-burger-bars bar2" />
        <span className="bm-burger-bars bar3" />
        <button className="bm-burger-span-btn" />
      </div>
    </nav>
  );
};
Header.propTypes = {
  // wheeling: PropTypes.bool.isRequired,
  current: PropTypes.number.isRequired,
  mouse: PropTypes.bool.isRequired,
  ArrowUp: PropTypes.bool.isRequired,
  ArrowDown: PropTypes.bool.isRequired,
  ArrowLeft: PropTypes.bool.isRequired,
  ArrowRight: PropTypes.bool.isRequired,
  size: PropTypes.string.isRequired,
  authenticated: PropTypes.bool.isRequired,
  mobileNav: PropTypes.bool.isRequired,
  popupOpen: PropTypes.bool.isRequired,
  navOpen: PropTypes.func.isRequired,
  scrollTo: PropTypes.func.isRequired
};
export default Header;



