import React, { Component } from "react";
import PropTypes from "prop-types";
import { PopupboxManager } from "react-popupbox";
import Fade from "react-reveal/Fade";
import Slides from "./SlidePortfolio";
import Grid from "./GridPortfolio";
export default class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentProject: 0
    };
  }

  changeProject = index => {
    this.setState({ currentProject: index });
  };
  openPopupbox = content => {
    PopupboxManager.open({ content });
  };
  prevProject = () => {
    this.setState(prevState => ({
      currentProject:
        prevState.currentProject == 0 ? 3 : prevState.currentProject - 1
    }));
  };
  nextProject = () => {
    this.setState(prevState => ({
      currentProject:
        prevState.currentProject == 3 ? 0 : prevState.currentProject + 1
    }));
  };
  render() {
    const { size, pRef, current } = this.props;
    return (
      <section className="portfolio" ref={pRef}>
        <Fade when={current == 1}>
          <div className="portfolio-inside">
            <div className="portfolio-headline">
              <i className="fas fa-images" />
              <span className="headline-title">Projects</span>
              <span className="headline-small">Feel free, choose one</span>
            </div>
            <div className="portfolio-gallery">
              {size !== "laptop" ? (
                <Grid size={size} openPopup={this.openPopupbox} />
              ) : (
                <Slides
                  currentProject={this.state.currentProject}
                  changeProject={this.changeProject}
                  openPopup={this.openPopupbox}
                />
              )}
            </div>

            {size == "laptop" ? (
              <div className="portfolio-navigation">
                <div
                  onClick={this.prevProject}
                  className="port-nav-btn prev-project"
                />
                <div
                  onClick={this.nextProject}
                  className="port-nav-btn next-project"
                />
              </div>
            ) : null}
          </div>
        </Fade>
      </section>
    );
  }
}
Portfolio.propTypes = {
  size: PropTypes.string.isRequired,
  pRef: PropTypes.func.isRequired,
  current: PropTypes.number.isRequired
};



