/* prittier-disable */
import React, { Component } from "react";
import PropTypes from "prop-types";
import Fade from "react-reveal/Fade";
import triangleData from "./TriangleData";
import animationData from "./AnimationData";

export default class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      interval: 0,
      aniOn: false,
      aniTr: [],
      finish: false
    };
  }

  componentWillReceiveProps(nextProps) {
    nextProps.current == 2 && !this.state.finish
      ? setTimeout(() => this.toggleAni(), 1000)
      : null;
  }

  exists = (arr, search) => {
    return arr.some(row => row.includes(search));
  };
  stepUp = () => {
    if (!this.state.finish) {
      if (this.state.interval < animationData.dataAni.length) {
        this.setState(
          prevState => ({
            aniTr: [
              ...prevState.aniTr,
              animationData.dataAni[this.state.interval][0]
            ]
          }),
          () =>
            this.setState(prevState => ({
              interval: prevState.interval + 1
            }))
        );
      } else {
        this.setState({
          finish: true
        });
      }
      requestAnimationFrame(this.stepUp);
    }
  };
  toggleAni = () => {
    if (!this.state.aniOn) {
      this.setState(
        {
          aniOn: true
        },
        () => this.stepUp()
      );
    }
  };
  createTriangle = (rIdx, gIdx, skip, side, amount) => {
    var group = [];
    for (var tIdx = 0; tIdx < amount; tIdx++) {
      group.push(
        <div
          className={`tri ${
            (tIdx + (side == "down" ? 0 : 1)) % 2 == 0
              ? this.state.aniTr.includes(
                  parseInt([rIdx, gIdx, tIdx].join(""))
                ) ||
                this.exists(
                  animationData.initAni,
                  parseInt([rIdx, gIdx, tIdx].join(""))
                )
                ? "tldc"
                : ""
              : this.state.aniTr.includes(
                  parseInt([rIdx, gIdx, tIdx].join(""))
                ) ||
                this.exists(
                  animationData.initAni,
                  parseInt([rIdx, gIdx, tIdx].join(""))
                )
                ? "tluc"
                : ""
          }`}
          style={{
            top: `${rIdx * 90 - (rIdx && rIdx * 10)}px`,
            left: `${(tIdx + skip) * 45}px`,
            opacity: `0.${
              this.exists(
                animationData.initAni,
                parseInt([rIdx, gIdx, tIdx].join(""))
              )
                ? animationData.initAni[
                    animationData.initAni.findIndex(
                      x => x[0] == parseInt([rIdx, gIdx, tIdx].join(""))
                    )
                  ][1]
                : this.state.aniTr.includes(
                    parseInt([rIdx, gIdx, tIdx].join(""))
                  )
                  ? animationData.dataAni[
                      animationData.dataAni.findIndex(
                        x => x[0] == parseInt([rIdx, gIdx, tIdx].join(""))
                      )
                    ][1]
                  : 0
            }
            `
          }}
          key={tIdx}
        >
          {/* <div
            style={{
              fontSize: 11,
              bottom: 30,
              left: 35,
              position: "absolute"
            }}
          >
            <span style={{ color: "yellow" }}>{rIdx}</span>
            <span style={{ color: "orange" }}>{gIdx}</span>
            <span style={{ color: "red" }}>{tIdx}</span>
          </div> */}
        </div>
      );
    }
    return group;
  };
  render() {
    const { aRef, current } = this.props;
    return (
      <section className="about" ref={aRef}>
        <Fade when={current == 2}>
          <div className="about-inside">
            <div className="about-triangle">
              {triangleData.map((rowData, rowIdx) => (
                <div className="t-row" key={rowIdx}>
                  {rowData.triData.map((groupData, groupIdx) => (
                    <div className="t-group" key={groupIdx}>
                      {this.createTriangle(
                        rowIdx,
                        groupIdx,
                        groupData[0],
                        groupData[1],
                        groupData[2]
                      )}
                    </div>
                  ))}
                </div>
              ))}
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

About.propTypes = {
  aRef: PropTypes.func.isRequired,
  current: PropTypes.number.isRequired
};



