import React from "react";
import logo from "../assets/colored.svg";
import PropTypes from "prop-types";
import Fade from "react-reveal/Fade";

const Home = ({ scrollTo, hRef, authenticated }) => (
  <section className="home" ref={hRef}>
    <Fade bottom when={authenticated}>
      <div className="home-inside">
        <img className="logo2" src={logo} />
        <div className="info">
          <span>Alex Ortiz.</span>
          <span>Front-end Web Developer.</span>
        </div>

        <a className="next" onClick={() => scrollTo(1)}>
          <div className="dr">
            <span className="text t1">Scroll</span>
            <span className="text t2">down...</span>
            <div className="startExplore" />
            <span className="round r1" />
            <span className="round r2" />
          </div>
        </a>
      </div>
    </Fade>
  </section>
);
Home.propTypes = {
  scrollTo: PropTypes.func.isRequired,
  hRef: PropTypes.func.isRequired,
  authenticated: PropTypes.bool.isRequired
};
export default Home;



