import React, { Component } from "react";
import PropTypes from "prop-types";
export default class Carousel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0
    };
  }
  prevImage = () => {
    this.setState(prevState => ({
      current: !prevState.current
        ? this.props.images.length - 1
        : prevState.current - 1
    }));
  };
  nextImage = () => {
    this.setState(prevState => ({
      current:
        prevState.current > this.props.images.length - 2
          ? 0
          : prevState.current + 1
    }));
  };
  render() {
    return (
      <div className="carousel">
        {this.props.images && (
          <i
            className="prevImage carousel-btn fas fa-chevron-left"
            onClick={this.prevImage}
          />
        )}
        <img
          src={
            this.props.images
              ? this.props.images[this.state.current]
              : "http://abqdental.com/wp-content/uploads/2016/10/orionthemes-placeholder-image.jpg"
          }
        />
        {this.props.images && (
          <i
            className="nextImage carousel-btn fas fa-chevron-right"
            onClick={this.nextImage}
          />
        )}
      </div>
    );
  }
}
Carousel.propTypes = {
  images: PropTypes.array.isRequired
};



