import React, { Component } from "react";
import Header from "./components/Header";
import Home from "./components/Home";
import Portfolio from "./components/Portfolio";
import About from "./components/About";
import Contact from "./components/Contact";
import Loading from "./components/Loading";
import { PopupboxContainer } from "react-popupbox";

import { debounce } from "throttle-debounce";
// import $ from "jquery";
import "./styles/main.css";
import { CSSTransition } from "react-transition-group";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // wheeling: false,
      current: 0,
      mouse: false,
      ArrowUp: false,
      ArrowDown: false,
      ArrowLeft: false,
      ArrowRight: false,
      size: "",
      authenticated: true,
      mobileNav: false,
      popupOpen: false
    };
    this.Sections = [];
    this.Authtime = 2000;
  }

  componentDidMount() {
    setInterval(() => this.setState({ time: Date.now() }), 100);
    document.addEventListener("keyup", this.navInfoKey);
    document.addEventListener("click", this.navInfoMouse);
    document.addEventListener("mousewheel", debounce(250, this.scrollDetect));
    window.addEventListener("resize", this.changeSize);
    this.changeSize();
    this.fakeAuth().then(() =>
      this.setState(
        {
          authenticated: true
        },
        () => {
          document.body.style.overflow = "auto";
        }
      )
    );
  }
  componentWillUnmount() {
    document.removeEventListener("keyup", this.navInfoKey);
    document.removeEventListener("click", this.navInfoMouse);
    window.removeEventListener("resize", this.changeSize);
  }
  componentWillUpdate(nextProps, nextState) {
    if (this.state.size !== nextState.size && this.state.mobileNav == true)
      return this.navOpen();
    if (this.state.authenticated !== nextState.authenticated)
      return this.Sections[0].scrollIntoView();
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.current !== this.state.current)
      return this.Sections[this.state.current].scrollIntoView({
        behavior: "smooth"
      });
  }
  fakeAuth = () => {
    return new Promise(resolve => setTimeout(resolve, this.Authtime));
  };
  navInfoKey = e => {
    if (["ArrowUp", "ArrowDown", "ArrowLeft", "ArrowRight"].includes(e.code)) {
      e.code == "ArrowUp" || e.code == "ArrowLeft"
        ? this.state.current > 0
          ? this.scrollToDirection("u")
          : null
        : e.code == "ArrowDown" || e.code == "ArrowRight"
          ? this.state.current < 3
            ? this.scrollToDirection("d")
            : null
          : null;
      this.setState(
        {
          [e.code]: true
        },
        () =>
          setTimeout(
            () =>
              this.setState({
                [e.code]: false
              }),
            500
          )
      );
    }
  };
  scrollToIndex = index => {
    this.setState({
      current: index
    });
  };
  repositionScroll = () => {
    this.Sections[this.state.current].scrollIntoView({
      behavior: "smooth"
    });
  };
  scrollToDirection = dir => {
    this.setState({ current: this.state.current + (dir == "u" ? -1 : 1) });
  };
  scrollDetect = e => {
    this.state.authenticated && Math.sign(e.wheelDeltaY) == "-1"
      ? this.state.current < 3
        ? this.scrollToDirection("d")
        : null
      : this.state.current > 0
        ? this.scrollToDirection("u")
        : null;
  };
  navInfoMouse = () => {
    this.setState(
      {
        mouse: true
      },
      () =>
        setTimeout(
          () =>
            this.setState({
              mouse: false
            }),
          500
        )
    );
  };
  changeSize = () => {
    var w =
      window.innerWidth ||
      document.documentElement.clientWidth ||
      document.body.clientWidth;
    let newSize = "mobile";
    if (w >= 824 && w < 1024) newSize = "tablet";
    if (w >= 1024) newSize = "laptop";
    if (newSize !== this.state.size) {
      this.setState({
        size: newSize
      });
    }
    this.repositionScroll();
  };

  navOpen = () => {
    this.setState(prevState => ({
      mobileNav: !prevState.mobileNav
    }));
  };

  render() {
    const popupboxConfig = {
      titleBar: {
        enable: false
      },
      fadeIn: true,
      fadeInSpeed: 500,
      onOpen: () => this.setState({ popupOpen: true }),
      onClosed: () => this.setState({ popupOpen: false }),
      overlayOpacity: "0.4"
    };
    return (
      <div className="app-container">
        <PopupboxContainer {...popupboxConfig} />
        <CSSTransition
          in={this.state.authenticated === false}
          timeout={300}
          classNames="preload"
          unmountOnExit
        >
          <Loading authenticated={this.state.authenticated} />
        </CSSTransition>
        <CSSTransition
          in={this.state.authenticated}
          appear={true}
          timeout={900}
          classNames="header"
          unmountOnExit
        >
          <Header
            {...this.state}
            navOpen={this.navOpen}
            scrollTo={this.scrollToIndex}
          />
        </CSSTransition>
        <div
          className={`main-container ${
            this.state.mobileNav ? "main-push" : ""
          }`}
        >
          <Home
            scrollTo={this.scrollToIndex}
            hRef={el => (this.Sections[0] = el)}
            authenticated={this.state.authenticated}
          />
          <Portfolio
            size={this.state.size}
            pRef={el => (this.Sections[1] = el)}
            current={this.state.current}
          />
          <About
            aRef={el => (this.Sections[2] = el)}
            current={this.state.current}
          />
          <Contact
            cRef={el => (this.Sections[3] = el)}
            current={this.state.current}
          />
        </div>
      </div>
    );
  }
}

export default App;



