import React from "react";
import Carousel from "./Carousel";
import { PopupboxManager } from "react-popupbox";
const popupData = (index, data) => {
  return (
    <div>
      <Carousel images={data.slideImages} />
      <div className="carousel-content">
        <span className="carousel-project"> {data.project}</span>
        <span className="carousel-smalldescr">{data.smallDescr}</span>
        <span className="carousel-description">{data.description}</span>
      </div>
      <i
        className="carousel-exit fas fa-times"
        onClick={PopupboxManager.close}
      />
    </div>
  );
};

export default popupData;



