import React from "react";
import PropTypes from "prop-types";
import Fade from "react-reveal/Fade";
const info = {
  number: "(413) 367-8399",
  email: "alexowebdev@gmail.com",
  dribble: "https://dribbble.com/alexofrontend",
  instagram: "#",
  twitter: "https://twitter.com/Alexodev_"
};
const Contact = ({ cRef, current }) => (
  <section className="contact" ref={cRef}>
    <Fade when={current == 3}>
      <div className="contact-inside">
        <div className="contact-headline">
          <i className="far fa-address-card" />
          <span className="head-c-title">Stay in touch</span>
          <span className="head-c-descr">Where can you find me ...</span>
        </div>
        <div className="body-contact">
          <div className="row personal">
            <div className="email-contact">
              <label>E-mail address:</label>
              <i className="icon far fa-envelope" />
              <a
                href={`mailto:${info.email}`}
                target="_top"
                className="email href"
              >
                {info.email}
              </a>
            </div>
            <div className="phone-contact">
              <label>Telephone:</label>
              <i className="icon fas fa-mobile-alt" />
              <span className="phone">{info.number}</span>
            </div>
          </div>
          <div className="row creative">
            <label>Creative media:</label>
            <div className="dribble-contact">
              <i className="icon fab fa-dribbble" />
              <a href={info.dribble} className="href dribble">
                Dribble
              </a>
            </div>
          </div>
          <div className="row social">
            <label>Social media:</label>
            <div className="instagram-contact">
              <i className="icon fab fa-instagram" />
              <a href={info.instagram} className="href instagram">
                Instagram
              </a>
            </div>
            <div className="twitter-contact">
              <i className="icon fab fa-twitter" />
              <a href={info.twitter} className="href twitter">
                Twitter
              </a>
            </div>
          </div>
        </div>
      </div>
    </Fade>
  </section>
);
Contact.propTypes = {
  cRef: PropTypes.func.isRequired,
  current: PropTypes.number.isRequired
};
export default Contact;



