import React from "react";
import PropTypes from "prop-types";
import galleryData from "./GalleryData";
import popupData from "./PopupData";

const Slides = ({ currentProject, changeProject, openPopup }) => (
  <div className="desktop-grid">
    <ul
      style={{
        marginLeft: -Math.abs(currentProject * 620)
      }}
    >
      {galleryData.map((data, index) => (
        <li
          key={index}
          className={currentProject === index ? "active" : ""}
          onClick={() => changeProject(index)}
        >
          <div
            className="img"
            onClick={
              index == currentProject
                ? () => openPopup(popupData(index, data))
                : null
            }
            style={{
              top: currentProject === index ? "-80px" : "0"
            }}
          />
          <div
            className="bottom"
            style={{
              bottom: currentProject === index ? "0" : "-80px",
              opacity: currentProject === index ? 1 : 0
            }}
          >
            <span className="project-name">
              {data.project}
              {data.link !== "" ? (
                <a href={data.link}>
                  <i className="fas fa-external-link-alt" />
                </a>
              ) : null}
            </span>
            <span className="project-descr">{data.type}</span>
          </div>
        </li>
      ))}
    </ul>
  </div>
);
Slides.propTypes = {
  currentProject: PropTypes.number.isRequired,
  changeProject: PropTypes.func.isRequired,
  openPopup: PropTypes.func.isRequired
};
export default Slides;



